.clipping-list {
  padding: 1.5rem;
}

.clipping-heading {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 1.5rem;
}

.clipping-grid {
  width: calc(100vw - 400px);
  column-count: 1;
  column-gap: 1.5rem;
  padding: 1rem;
}

/* ≥ 600px: 2 kolumny */
@media (min-width: 600px) {
  .clipping-grid {
    column-count: 1;
  }
}

/* ≥ 900px: 3 kolumny */
@media (min-width: 900px) {
  .clipping-grid {
    column-count: 2;
  }
}

/* ≥ 1200px: 4 kolumny */
@media (min-width: 1200px) {
  .clipping-grid {
    column-count: 3;
  }
}


.clipping-card {
  display: inline-block;
  width: 90%;
  margin-bottom: 1.5rem;
  background-color: #ffffff;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  padding: 1rem; /* Zmniejszony padding */
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  transition: box-shadow 0.2s ease, transform 0.2s ease;
}

.clipping-card:hover {
  box-shadow: 0 4px 14px rgba(0, 0, 0, 0.12);
  transform: translateY(-2px);
}

.clipping-header {
  margin-bottom: 0.75rem;
}

.book-title {
  font-weight: bold;
  font-size: 1rem;
}

.book-author {
  font-size: 0.85rem;
  color: #6b7280;
}

.clipping-content {
  flex-grow: 1;
  font-size: 0.95rem;
  margin-bottom: 0.75rem;
  color: #374151;
  word-break: break-word;
}


.clipping-footer {
  display: flex;
  justify-content: space-between; /* left and right */
  align-items: center; /* vertical centering */
  margin-top: 0.5rem;
  color: #9ca3af;
  font-size: 0.75rem;
}

.clipping-actions i {
  cursor: pointer;
  font-size: 1.1rem;
  margin-left: 0.5rem;
  color: #6b7280;
  transition: color 0.2s;
}

.clipping-actions i:hover {
  color: #111827;
}

.clipping-filter {
  width: 85%;
  padding: 6px 8px;
  margin-bottom: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
}

.clipping-tags {
  font-size: 0.85rem;
  color: #6b7280;
}

.tag {
  font-size: 0.85rem;
  color: #4b5563;
  background-color: #f3f4f6; 
  border-radius: 2px;
  padding: 2px 5px;
  margin-right: 4px;
  display: inline-block;
  user-select: none;
  transition: background-color 0.2s ease;
}

.tag:hover {
  background-color: #e5e7eb; 
  cursor: default;
}

.tag-menu input {
  width: 90%;
  padding: 6px 8px;
  margin-bottom: 12px;
  margin-top: 7px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
  padding: 5px;
}



.tag-suggestions {
  position: absolute;
  background-color: #f9fafb; /* bardzo jasny, neutralny kolor tła */
  border: 1px solid #d1d5db; /* jasnoszare, delikatne obramowanie */
  border-radius: 6px;
  margin-top: 0.25rem;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08); /* bardzo subtelny cień */
  z-index: 10;
  width: 80%;
  max-height: 180px; /* ograniczenie wysokości, z scrollbar */
  overflow-y: auto;
  font-size: 0.9rem;
  color: #374151; /* ciemniejszy, ale stonowany tekst */
  list-style: none;
  padding-left: 0;  
  margin: 0;        
}

.tag-suggestions li {
  padding: 6px 10px;
  cursor: pointer;
  transition: background-color 0.2s ease;
  border-radius: 4px;
}

.tag-suggestions li:hover {
  background-color: #e5e7eb; /* bardzo jasny szary podświetlenie */
  color: #111827; /* ciemniejszy tekst na hover */
}
